import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import styled from 'styled-components';
import { AuthFormError, Button, Input } from '../../components';
import { request } from '../../utils';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setUser } from '../../actions';
import { useResetForm } from '../../hooks/use-reset-form';
import { Navigate } from 'react-router-dom';
import { ROLE } from '../../constants/role';
import { selectUserRole } from '../../selectors';

const authFormSchema = yup.object().shape({
	login: yup
		.string()
		.required(' ')
		.email('  .   email-')
		.min(3, '  .  3 ')
		.max(15, '  .  15 '),
	password: yup
		.string()
		.required(' ')
		.matches(
			/^[\w#%]+$/,
			'  .   ,    #  %'
		)
		.min(5, '  .  5 ')
		.max(30, '  .  30 '),
});

const LoginContainer = ({ className }) => {
	const {
		register,
		reset,
		handleSubmit,
		formState: { errors },
	} = useForm({
		defaultValues: {
			login: '',
			password: '',
		},
		resolver: yupResolver(authFormSchema),
	});

	const [serverError, setServerError] = useState(null);
	const dispatch = useDispatch();
	const roleId = useSelector(selectUserRole);

	useResetForm(reset);

	const onSubmit = ({ login, password }) => {
		request('/login', 'POST', { login, password }).then(
			({ error, user }) => {
				if (error) {
					setServerError(` : ${error}`);
					return;
				}

				dispatch(setUser(user));
				sessionStorage.setItem('userData', JSON.stringify(user));
			}
		);
	};

	const formError = errors?.login?.message || errors?.password?.message;
	const errorMessage = formError || serverError;

	if (roleId !== ROLE.GUEST) {
		return <Navigate to="/" />;
	}

	return (
		<div className={className}>
			<h2></h2>
			<form onSubmit={handleSubmit(onSubmit)}>
				<Input
					type="text"
					placeholder="Email..."
					{...register('login', {
						onChange: () => setServerError(null),
					})}
				/>
				<Input
					type="password"
					placeholder="..."
					{...register('password', {
						onChange: () => setServerError(null),
					})}
				/>
				<Button type="submit" width="120px" disabled={!!formError}>
					
				</Button>
				{errorMessage && <AuthFormError>{errorMessage}</AuthFormError>}
			</form>
		</div>
	);
};

export const Login = styled(LoginContainer)`
	display: flex;
	align-items: center;
	flex-direction: column;
	margin-top: 15%;

	& > form {
		display: flex;
		align-items: center;
		padding: 30px;
		flex-direction: column;
		width: 500px;
		border: 3px solid #4a148c;
	}
`;
